import React, { useState } from 'react';
import Card from './Card'

import '../css/Player.scss';

// @player: { name: 'Peter', characterCards: [ ... ], money: 5 .. }
function Player({ player, index, playersAPI, crownPlayer, cards, setCards, cardsAPI }) {

  //  + + + + MODEL + + + +
  /* 
  {
      
  }
  */
 
  return ( 
          <React.Fragment>
            <div className='row'>
              <div className='col-6'>
                <h3 className="d-block">{player.name}</h3>              
              </div>
              <div className='col-3'>
                { (crownPlayer === index )? (<img className='Card__icon img-fluid h-100' src="/imgs/crown.png" height='50' alt='crown'/> ) : null }
              </div>
              <div className='col-3'>
                $ {player.money}
              </div>
            </div>

            <div className='row'>            
              <div className='col-2 extra-small-cards'>                
                { player.characterCards.map( ( cardIndex, i ) => {
                  const card = cards[cardIndex];
                  return (
                    <Card card={card} key={i} place={'character-private'} />
                    )
                  }) }
              </div>
              <div className='col-10'>              
                <div className='row extra-small-cards'>                  
                  { player.districtCards.filter( cardIndex => !cards[cardIndex].is_built )
                        .map( ( cardIndex, i ) => {
                            const dCard = cards[cardIndex];
                            // TODO: make clicable if the currentPlayer has money to buy it!                            
                            return (
                              <Card key={'cc-'+cardIndex} place={'player-private'}  card={dCard} allowedToBuild={playersAPI.playerCanBuild(index, cardIndex)} cardsAPI={cardsAPI} />
                              )
                    }) }
                </div>
                <hr />
                <div className='row extra-small-cards'>
                { player.districtCards.filter( cardIndex => cards[cardIndex].is_built ).map( ( cardIndex, i ) => {
                    const card = cards[cardIndex];
                    return (
                      <Card card={card} key={i} place={'player-built'} player={player} cards={cards} setCards={setCards} />
                      )
                    }) }
                </div>
              
              </div>
            </div>
          </React.Fragment>
         );
}
       
export default Player;
