// This is called in CardsDeck.js, inside fn viewWrapperCharacter(). 
import React from 'react';
import '../css/NextTurnButton.scss';

function NextTurnButton({  hand, gameAPI }) {

  
  const handleClick = () => {
    gameAPI.moveToNextPlayerOrStage(); // it should alway be moveToNextPlayer()
  }

  // we show the button if: we can move to the next turn
  if (hand.canMoveNext.can) {
    return <button className="btn btn-danger mb-3" onClick={handleClick}>
      Next turn
    </button>
  }
  return null;
}

export default NextTurnButton;