import React, { useState } from 'react'
import NextTurnButton from './NextTurnButton'
import { renderObject } from "../helpers";

function TopNav({ cards, currentPlayer, hand, players, gameAPI, gameOptions }) {

  const [open, updateOpen] = useState(false);

  // returns player object
  const getCurrentPlayer = () => players[currentPlayer]? players[currentPlayer] : {} ;

  
  return (
    <div className={'TopNav container ' + (open ? 'open' : 'closed')}>
      <div className='row text-center'>
        <small className={ 'card col-4' }> { getCurrentPlayer().name } ({currentPlayer})</small>
        <small className={ 'card col-4 bg-' + (hand.canMoveNext.can ? 'success' : 'danger ') } > can pass turn </small>
        <small className={ 'card col-4 bg-' + (hand.canMoveNext.must ? 'success' : 'danger ') } > turn is finished </small>
      </div>
        
        
      <button className={'btn btn-primary btn-sm'} onClick={gameAPI.initDemoGame}>Load cards</button>
      <button className={'btn btn-secondary btn-sm ' + ( cards.length? '' : 'd-none' ) } onClick={ () => gameAPI.startGame() }>Start game</button>
    </div>
  );
}

export default TopNav;
